import React from 'react';
import ReactDOM from 'react-dom';

class Head extends React.Component {
  render () {
    return (
      <div>
        <div>My Page</div>
        <hr />
      </div>
    );
  }
}

class Who extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      name: "rita",
      mail: "wkryz1204@gmail.com",
      who: false,
    };
  };

  handleClick () {
    this.setState({
      who: !this.state.who,
    });
  };

  render () {
    return (
      <div onClick={() => this.handleClick()}>
        {this.state.who ? "Who ▼" : "Who ▶︎"}
        {this.state.who ? <li>Name: {this.state.name}</li> : <div>&nbsp;</div>}
        {this.state.who ? <li>Mail: {this.state.mail}</li> : <div>&nbsp;</div>}
      </div>
    );
  }
}

class Contents extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      contents: false,
    };
  };

  handleClick () {
    this.setState({
      contents: !this.state.contents,
    });
  };

  render () {
    return (
      <div onClick={() => this.handleClick()}>
      {this.state.contents ? "Contents ▼" : "Contents▶︎"}
      {this.state.contents ? <li><a href="https://github.com/ritarock">github</a></li> : <div>&nbsp;</div>}
      {this.state.contents ? <li><a href="http://ritarock.hatenablog.com/">hatena blog</a></li> : <div>&nbsp;</div>}
      {this.state.contents ? <li><a href="https://qiita.com/ritarock">qiita</a></li> : <div>&nbsp;</div>}
      <br />
      </div>
    );
  }
}

class Main extends React.Component {
  render () {
    return (
      <div>
      <Head />
      <Who />
      <Contents />
      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <Main />,
  document.getElementById('root')
);

